<script setup>
  import { VueFlow } from '@braks/vue-flow';
  import { ref } from 'vue';
  import Controls from './Controls.vue';
  import { useVueFlow } from '@braks/vue-flow';

  const flowKey = 'example-flow';
  const { setNodes, setEdges, setTransform } = useVueFlow();

  const vRestoreDirective = {
    created: () => {
      const flow = JSON.parse(localStorage.getItem(flowKey));
  
    if (flow) {
      const [x = 0, y = 0] = flow.position;
      setNodes(flow.nodes);
      setEdges(flow.edges);
      setTransform({ x, y, zoom: flow.zoom || 0 });
      }
    }
  }
  
  const elements = ref([
      //Нода - зарегестрированный пользователь
      { id: '1', type: 'input', label: 'Никита', position: { x: 0, y: 0 } },

      // Дефолтные ноды, имеющие и input и output (родители пользователя)
      { id: '2', label: 'Мать', position: { x: 300 , y: 300 }, },
      { id: '3', label: 'Отец', position: { x: -300, y: 300 } },

      // Оutput ноды (родители родителей)
      { id: '4', type: 'output', label: 'Бабушка', position: { x: -500, y: 600 } },
      { id: '5', type: 'output', label: 'Дедушка', position: { x: -100, y: 600 } },
      { id: '6', type: 'output', label: 'Бабушка', position: { x: 100, y: 600 } },
      { id: '7', type: 'output', label: 'Дедушка', position: { x: 500, y: 600 } },

      // Связи
      { id: 'e1-3', source: '1', target: '3' },
      { id: 'e1-2', source: '1', target: '2' },
      { id: 'e2-6', source: '2', target: '6' },
      { id: 'e2-7', source: '2', target: '7' },
      { id: 'e3-4', source: '3', target: '4' },
      { id: 'e3-5', source: '3', target: '5' },

      ])
      
  </script>
  
  <template>
    <VueFlow v-model="elements" v-restore-directive>
      <Controls />
    </VueFlow>
  </template>

  <style>
    
  </style>