<!-- eslint-disable vue/multi-word-component-names -->
<script setup>
  import { useVueFlow } from '@braks/vue-flow';
  
  const flowKey = 'example-flow';
  
  const { toObject } = useVueFlow();
  
  const onSave = () => {
    localStorage.setItem(flowKey, JSON.stringify(toObject()))
  };
  
  </script>
  
  <template>
    <div class="save__controls">
      <button style="background-color: #33a6b8" @click="onSave">save</button>
    </div>
  </template>

  <style>
    @import '@braks/vue-flow/dist/theme-default.css';

    button {
      cursor: pointer;
    }

    .save__controls {
      position: absolute;
      top: 100px;
      left: 100px;
      z-index: 100;
    }
    @import 'https://cdn.jsdelivr.net/npm/@braks/vue-flow@0.4.40/dist/style.css';
    @import 'https://cdn.jsdelivr.net/npm/@braks/vue-flow@0.4.40/dist/theme-default.css';

html,
body,
#app {
  margin: 0;
  height: 100%;
}

#app {
  font-family: 'JetBrains Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

.vue-flow__minimap {
  transform: scale(75%);
  transform-origin: bottom right;
}

.save__controls {
  position: absolute;
  left: 10px;
  top: 10px;
  z-index: 4;
  font-size: 12px;
}

.save__controls button {
  margin-left: 5px;
  padding: 5px;
  border-radius: 5px;
  font-weight: 700;
  text-transform: uppercase;
  color: #fff;
  -webkit-box-shadow: 0px 5px 10px 0px rgba(0,0,0,.3);
  box-shadow: 0 5px 10px #0000004d;
  cursor: pointer
};

.save__controls button:hover {
  opacity: .8;
  transform: scale(105%);
  transition: .25s all ease-in-out;
}
  </style>